#radio{
	text-align: left;
	color: #222;
	text-shadow: 0px 2px 3px #555;
	float: left;

}

.radio{
	
	padding: 20px;
}

.radio {
	padding-top: 80px;
	padding-bottom: 76px;
	background: #666;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    
}

.radio:hover {
    background-color: #4CAF50; /* Green */
    color: white;
}


